* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

body .light {
  background: #f2f9ff;
  min-width: 100vw;
  min-height: 100vh; }
  body .light#blue {
    background: #f2f9ff; }
    body .light#blue .blue {
      border: 2px solid #8e8e8e; }
  body .light#green {
    background: #f2fff4; }
    body .light#green .green {
      border: 2px solid #8e8e8e; }
  body .light#pink {
    background: #fff2f5; }
    body .light#pink .pink {
      border: 2px solid #8e8e8e; }

body .wrapper {
  overflow: auto; }

body .modal {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  min-height: 300px;
  height: auto;
  margin: auto;
  background: #fff;
  border-radius: 8px;
  padding: 10px;
  z-index: 999;
  visibility: hidden !important;
  opacity: 0 !important;
  text-align: left;
  box-shadow: 0 0 15px 1000px rgba(0, 0, 0, 0.35);
  transition: all .3s ease; }
  body .modal.open {
    visibility: visible !important;
    opacity: 1 !important;
    box-shadow: 0 0 15px 1000px rgba(0, 0, 0, 0.12); }
  body .modal .close {
    width: 28px;
    height: 28px;
    display: block;
    float: right;
    cursor: pointer; }
  body .modal .color {
    width: 25px;
    height: 25px;
    display: block;
    border-radius: 50%;
    background: rosybrown;
    margin: 0 7px 0 0;
    float: left;
    border: 2px solid #f2f2f2;
    cursor: pointer; }
    body .modal .color.blue {
      background: #364684; }
    body .modal .color.green {
      background: #265f2b; }
    body .modal .color.pink {
      background: #65293e; }

body header img {
  display: block;
  max-width: 300px; }
  body header img.logo {
    display: block;
    width: 230px;
    height: 47;
    padding: 20px; }
  body header img.settingsIcon {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 34px;
    height: 34px;
    cursor: pointer; }

body .content {
  display: block;
  margin: 100px auto;
  width: 70%;
  max-width: 700px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.07);
  padding: 0 1px 30px 1px;
  border: 5px; }
  body .content .inputWrapper {
    overflow: auto;
    margin: 8px; }
  body .content .itemForm {
    overflow: auto; }
    body .content .itemForm input {
      width: 75%;
      float: left;
      border: 1px solid #e4e4e4;
      background: #fbfbfb;
      padding: 10px 7px;
      font-size: 17px;
      border-radius: 3px;
      transition: all 0.2s ease; }
    body .content .itemForm a#submit {
      min-width: 20%;
      width: 22%;
      float: right;
      padding: 13px 10px;
      background: #364684;
      color: #fff;
      border: none;
      font-weight: 700;
      text-align: center;
      font-size: 13px;
      text-transform: uppercase;
      border-radius: 3px;
      cursor: pointer;
      transition: all 0.3s ease; }
      body .content .itemForm a#submit:before {
        content: "";
        display: inline-block;
        background: url(/img/plus.svg) no-repeat !important;
        background-size: cover;
        height: 15px;
        width: 16px;
        margin: 0 7px -2px 0; }
  body .content .sections {
    overflow: auto; }
    body .content .sections .sectionWrapper {
      background: #fff;
      display: inline-block;
      border: 1px solid #d3d3d3;
      border-radius: 25px;
      padding: 3px 20px 5px 20px;
      position: relative;
      cursor: pointer;
      margin: 6px 14px 0 0; }
      body .content .sections .sectionWrapper.current {
        background: #2d67ce;
        color: #fff; }
      body .content .sections .sectionWrapper img.close {
        width: 17px;
        height: 17px;
        margin-left: 10px;
        margin-bottom: -2px; }
  body .content .dynamicSection {
    text-align: left;
    opacity: 0;
    visibility: hidden;
    position: fixed;
    background: #fff;
    padding: 20px 30px;
    top: 45%;
    left: 0;
    right: 0;
    z-index: 999;
    max-width: 800px;
    margin: auto;
    box-shadow: 0px 0px 15px 1000px rgba(0, 0, 0, 0.12);
    border-radius: 5px;
    transition: all 0.3s ease; }
    body .content .dynamicSection.open {
      opacity: 1;
      visibility: visible; }
    body .content .dynamicSection input {
      width: 75%;
      float: left;
      border: 1px solid #e4e4e4;
      background: #fbfbfb;
      padding: 10px 7px;
      font-size: 17px;
      border-radius: 3px;
      transition: all 0.2s ease; }
    body .content .dynamicSection a {
      min-width: 20%;
      width: 22%;
      float: right;
      padding: 13px 10px;
      background: #364684;
      color: #fff;
      border: none;
      text-align: center;
      font-weight: 700;
      font-size: 13px;
      text-transform: uppercase;
      border-radius: 3px;
      cursor: pointer;
      transition: all 0.3s ease; }
      body .content .dynamicSection a:hover {
        background: #28345f; }
  body .content .todoItemWrapper {
    margin: 8px;
    padding: 20px 0; }
    body .content .todoItemWrapper h2 {
      display: inline; }
    body .content .todoItemWrapper #addSections {
      float: right; }
    body .content .todoItemWrapper .sections {
      margin-top: 10px;
      margin-bottom: 30px; }
  body .content #addSections {
    text-align: left;
    display: inline-block;
    color: #364684;
    font-weight: 600;
    cursor: pointer;
    margin-bottom: 10px;
    transition: all 0.3s ease; }
    body .content #addSections:hover {
      color: #28345f; }
    body .content #addSections:before {
      content: "";
      background: url(/img/plus-icon.svg) !important;
      background-size: cover;
      height: 15px;
      width: 16px;
      margin: 0 7px -2px 0;
      display: inline-block; }
  body .content .todoItem {
    list-style: none;
    margin: 6px 0;
    font-size: 17px;
    background: #fcfcfc;
    border: 1px solid #e8e8e8;
    border-radius: 5px;
    padding: 13px;
    height: auto;
    max-height: 160px;
    cursor: pointer;
    height: auto;
    max-height: 160px;
    position: relative;
    transition: all 0.3s ease; }
    body .content .todoItem.favorite {
      border: 1px solid #f3e7be;
      background: #fffad6; }
    body .content .todoItem.done {
      opacity: 0.6; }
      body .content .todoItem.done .done {
        background: #364684; }
    body .content .todoItem p.todoItemContent {
      display: inline-block;
      float: left;
      margin: 0; }
    body .content .todoItem .edit {
      float: right; }
    body .content .todoItem .done {
      width: 23px;
      height: 23px;
      border-radius: 50%;
      border: 2px solid #364684;
      display: inline-block;
      margin-right: 15px;
      float: left; }
    body .content .todoItem .actionArea {
      text-align: right;
      overflow: auto;
      visibility: hidden;
      opacity: 0;
      height: auto;
      max-height: 0;
      transition: all 0.3s ease; }
      body .content .todoItem .actionArea a {
        margin: 0 8px; }
        body .content .todoItem .actionArea a img {
          width: 29px;
          height: 29px; }
    body .content .todoItem.opened .actionArea {
      display: block;
      visibility: visible;
      height: auto;
      max-height: 200px;
      margin-top: 15px;
      opacity: 1;
      transition: all 0.3s ease; }
  body .content .changeSection .sectionWrapper {
    margin-top: 20px; }
    body .content .changeSection .sectionWrapper span {
      background: #fff;
      display: inline-block;
      border: 1px solid #d3d3d3;
      border-radius: 25px;
      padding: 3px 20px 5px 20px;
      position: relative;
      cursor: pointer;
      margin: 6px 14px 0 0; }
      body .content .changeSection .sectionWrapper span.current {
        background: #2d67ce;
        color: #fff; }

body footer {
  text-align: center; }
  body footer a {
    line-height: 165%;
    text-decoration: none;
    color: #000; }
    body footer a.madeWithLove {
      opacity: 0.7;
      font-size: 14px;
      margin-top: 10px;
      display: inline-block; }

body .dark {
  background-color: #000 !important;
  color: #fff;
  min-width: 100vw;
  min-height: 100vh; }
  body .dark .content {
    background: #202020; }
  body .dark .modal {
    background: #202020; }
  body .dark input#box {
    border: 1px solid #707070;
    background: #333333;
    color: #fff;
    transition: all 0.2s ease; }
  body .dark .todoItem {
    border: 1px solid #0b0b0b;
    background: #333333; }
  body .dark footer a {
    color: #ffffff; }

@media only screen and (max-width: 1080px) {
  body .logo {
    display: block;
    width: 70%;
    max-width: 210px; }
  body #settings {
    position: absolute;
    top: 20px;
    right: 20px;
    left: auto; }
  body .modal {
    width: 100%;
    max-width: 100%;
    box-shadow: 0px 10px 3000px 2000px rgba(0, 0, 0, 0.12); }
  body .startModal {
    position: absolute;
    top: 100px;
    bottom: 100px;
    overflow: auto; }
  body .startModal .feature {
    width: 50%; }
  body .content {
    width: 100%; }
  body .content .dynamicSection {
    padding: 20px 15px; }
  body .content .itemWrapper {
    padding-left: 9px;
    padding-right: 9px; }
  body .content .inputWrapper input[type="text"] {
    width: 100%;
    background: #f5f5f5;
    width: 86%;
    float: left; }
  body .content .inputWrapper input[type="button"] {
    width: 100%;
    margin-top: 10px;
    padding: 8px; }
  body .content .inputWrapper a#submit {
    width: 50px;
    max-width: 12%;
    min-width: 0;
    padding: 13px 0;
    font-size: 0; }
  body .content .inputWrapper a#submit:before {
    margin: 0;
    height: 15px;
    width: 15px; }
  body .content .dynamicSection input {
    width: 100%; }
  body .content .dynamicSection a {
    width: 100%;
    display: block;
    margin-top: 10px;
    padding: 8px; }
  body .content .removeSections {
    color: #e62525;
    font-weight: 600; }
  body #addSections {
    float: none !important;
    margin-top: 10px; } }
